import { z } from 'zod';

export const loginSchema = z.object({
  email: z
    .string()
    .min(1, 'El email es requerido')
    .email('Por favor, ingresa un email v√°lido'),
  password: z
    .string()
    .min(1, 'La contrase√±a es requerida')
    .min(8, 'La contrase√±a debe tener al menos 8 caracteres'),
  rememberMe: z.boolean(),
});

export const registerSchema = z
  .object({
    firstName: z
      .string()
      .min(1, 'El nombre es requerido')
      .max(50, 'El nombre debe tener m√°ximo 50 caracteres'),
    lastName: z
      .string()
      .min(1, 'El apellido es requerido')
      .max(50, 'El apellido debe tener m√°ximo 50 caracteres'),
    email: z
      .string()
      .min(1, 'El email es requerido')
      .email('Por favor, ingresa un email v√°lido'),
    password: z
      .string()
      .min(1, 'La contrase√±a es requerida')
      .min(8, 'La contrase√±a debe tener al menos 8 caracteres')
      .regex(
        /^(?=.*[A-Z])(?=.*[a-z])(?=.*\d)[A-Za-z\d]/,
        'La contrase√±a debe contener al menos una may√∫scula, una min√∫scula y un n√∫mero'
      ),
    confirmPassword: z
      .string()
      .min(1, 'La confirmaci√≥n de contrase√±a es requerida')
      .min(8, 'La confirmaci√≥n de contrase√±a debe tener al menos 8 caracteres'),
    user_type: z
      .string()
      .min(1, 'Debe seleccionar un tipo de usuario')
      .refine(
        (value) => value === 'teacher' || value === 'student',
        'Tipo de usuario no v√°lido'
      ),
    terms: z.boolean().refine((value) => value === true, {
      message: 'Debe aceptar los t√©rminos y condiciones para continuar',
    }),
  })
  .refine((data) => data.password === data.confirmPassword, {
    message: 'Las contrase√±as no coinciden',
    path: ['confirmPassword'],
  });
